@mixin todoItemText() {
  font-family: "Chalkboard SE", "Chalkboard", "Patrick Hand", cursive;
  font-size: 27px;
  text-transform: uppercase;
  user-select: none;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@mixin placeholder {
  ::-webkit-input-placeholder {
    @content;
  }
  :-moz-placeholder {
    @content;
  }
  ::-moz-placeholder {
    @content;
  }
  :-ms-input-placeholder {
    @content;
  }
}

a {
  font-weight: bold;
  color: #2c47e2;
  text-decoration: none;
}

main {
  max-width: 688px;
  margin: 0 auto;

  & > * {
    margin-bottom: 30px;
  }

  & > header {
    margin-bottom: 60px;
  }
}

#text-block {
  background: #f6f6f6;
  padding: 30px;
  border-radius: 10px;

  & > * {
    margin-bottom: 15px;
    line-height: 1.7;
  }
}

#todo-list {
  padding: 30px;
}

ol {
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  & > li {
    display: flex;
    flex-direction: row;
    --item-color: #000;
    transition: transform 300ms ease;
    transform-origin: 0 50%;
    cursor: pointer;
    margin-bottom: 15px;

    & > .todo-item-bullet {
      position: relative;
      width: 30px;
      &::before {
        content: "";
        display: block;
        position: absolute;
        width: 17px;
        height: 17px;
        top: 13px;
        border-radius: 50%;
        background: var(--item-color);
      }
    }

    & > .todo-item-title {
      @include todoItemText;
      position: relative;
      color: var(--item-color);
      transition: transform 300ms ease;

      &::after {
        content: "";
        width: 0%;
        border-bottom: 2px solid;
        position: absolute;
        top: 50%;
        left: -2%;
        transform: translateY(-50%);
      }
    }

    &.done {
      transform: scale(0.8);
      transition: transform 300ms 450ms ease;

      & > .todo-item-title::after {
        transition: width 300ms ease;
        width: 105%;
      }

      &:nth-of-type(1n) {
        --item-color: #0093bc;
      }
      &:nth-of-type(2n) {
        --item-color: #9f3b8c;
      }
      &:nth-of-type(3n) {
        --item-color: #d22a5e;
      }
      &:nth-of-type(4n) {
        --item-color: #f1641e;
      }
      &:nth-of-type(5n) {
        --item-color: #ffc444;
      }
      &:nth-of-type(6n) {
        --item-color: #35bd42;
      }
    }
  }
}

input {
  width: 100%;
  appearance: none;
  border: 0;
  outline: none;
  @include todoItemText;
}

@include placeholder {
  color: #000;
  opacity: 0.3;
}
